body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url('/assets/background.png');


}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  overflow: hidden;
}

a {
  text-decoration: none;
  color: #444;
}

h1, h2 {
  text-transform: capitalize;
  margin: 0;
}

.App {
  text-align: center;
}


footer {
    min-height: calc(50vh - 10px);
    width: 100%;
    background-color: #fff;
    border-top: 10px solid #000;
    position: relative;
    z-index: 2;
  }
  
footer small {
  display: none;
  line-height: 15vh;
  font-size: 14px;
  }
  
.App-header {
    transition: all 1s;
    background-color: #C32025;
    position: relative;
    z-index: 3;
    min-height: calc(50vh - 10px);
    display: grid;
    align-items: center;
    justify-items: stretch;
    grid-template-columns: 1fr;
    font-size: calc(10px + 2vmin);
    color: white;
    border-bottom: 10px solid #000;
  }

  .App-header h1 {
    font-size: 3vw;
    position: absolute;
    left: calc(50%);
    color: #fff;
    transition-property: -webkit-transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    transition-duration: 1s;
  }


.logo {
    width: 100px;
    height: 100px;
    border: 10px solid #000;
    border-radius: 50%; 
    background-color: #fff;
    position: absolute;
    bottom: -60px;
    left: calc(50% - 60px);
    cursor: pointer;
    z-index: 3;
  }

.logo:hover {
  -webkit-animation: none;
          animation: none;
}

@-webkit-keyframes shadow-pulse
{
  0% {
    -webkit-box-shadow: 0 0 0 0px rgba(195, 195, 195, 0.6);
  }
  100% {
    -webkit-box-shadow: 0 0 0 20px rgba(195, 195, 195, 0);
  }
}

@keyframes shadow-pulse
{
  0% {
    box-shadow: 0 0 0 0px rgba(195, 195, 195, 0.6);
  }
  100% {
    box-shadow: 0 0 0 20px rgba(195, 195, 195, 0);
  }
}
.wrapp.single-pokemon {
    min-height: 61vh;
}

.container.single-pokemon {
    margin: 60px auto;
    background-color: rgba(255, 255, 255, 1);
    box-shadow: 0 0 6px;
    padding: 20px 0;
    box-sizing: border-box
}

.container.single-pokemon h1 {
    margin: 0;
}

.container.single-pokemon h2 {
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    display: inline-block;
    padding: 5px 10px;
    border-radius: 5px;
    background: #a3a3a3;
    margin: 20px 0;
}

.container.single-pokemon h2:hover {
    background: #cecece;
    color: #222
}

.pokemon-moves-holder {
    transition: all .5s;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 2px;
}

.pokemon-stats-holder {
    transition: all .6s;
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;
}

.pokemon-image-holder {
    transition: all .6s;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center
}

.pokemon-image-holder img[alt="sprite"]{
    width: 130px;
}

.pokemon-image-holder h4 {
    margin: 0 0 10px 0;
}

.pokemon-stats{
    margin-top: 20px;
}

.pokemon-stats h2, .pokemon-stats p{
    margin-top: 0;
}

.pokemon-types {
    width: 60px;
    height: 60px;
}



.wrapp {
    transition: 1s;
    min-height: calc(140vh);
}
.container {
    width: 80%;
    margin: 0 auto;
    z-index: 1;
}

.pokemon-holder {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    margin: 50px;
}

.pokemon-holder div{
    background-color: rgba(255, 255, 255, .6);
    margin: 5px;
    box-shadow: 0 0 6px;
    cursor: pointer;
    min-height: 183px;
}
.pokemon-holder div:hover{
    background-color: rgba(255, 255, 255, .9);
    box-shadow: 0 0 10px;
}
.pokemon-holder div:active{
    background-color: rgba(255, 255, 255, .9);
    box-shadow: inset 0 0 10px;
}

.filter-by-color {
    display: inline-block;
    background-color: rgba(255, 255, 255, .6);
    padding: 5px 20px;
}

.color-btns {
    display: inline-block; 
    width: 20px;
    height: 20px;
    border-radius: 3px; 
    margin-right: 10px;
    border: 1px solid #cecece;
    cursor: pointer;
}

.color-btns:hover {
    border: 1px solid #444;
}

.all-colors {
    display: inline-block;
    width: 20px;
    height: 20px;
    background-color: rgb(255, 0, 0);
    border-radius: 3px; 
    margin-right: 10px;
    border: 1px solid #cecece;
    cursor: pointer;
}

.all-colors:hover {
    background-color: rgb(225, 0, 255);
    /* border: 1px solid #444; */
}

.all-colors::before {
    content: " ";
    display: inline-block; 
    position: relative;
    left: 3px;
    width: 7px;
    height: 20px;
    background-color: rgb(0, 255, 0);
}

.all-colors:hover::before {
    background-color: rgb(255, 238, 0);
}

.all-colors:hover::after {
    background-color: rgb(148, 148, 148);
}

.all-colors::after {
    content: " ";
    display: inline-block;
    position: relative;
    left: 3px; 
    width: 7px;
    height: 20px;
    background-color: rgb(0, 0, 255);
}

.pagination-btns {
    padding: 4px 13px;
    border: 1px solid #cecece;
    margin-right: 10px;
    background-color: rgba(255, 255, 255, .9);
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.pagination-btns.hide {
    display: none;
}

.pagination-btns.hide:last-of-type {
    display: inline-block;
}

@media only screen and (min-width: 1001px) {
    .container.single-pokemon {
        width: 700px;
    }
}
@media only screen and (max-width: 1000px) {
    .container.single-pokemon {
        width: 700px;
    }
    .App-header h1 {
        -webkit-transform: translateX(96%);
                transform: translateX(96%)
    }
    .pokemon-holder, .pokemon-moves-holder {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }
}
@media only screen and (max-width: 800px) {
    .container.single-pokemon {
        width: 80%;
    }
    .pokemon-holder, .pokemon-moves-holder {
        grid-template-columns: 1fr 1fr 1fr;
    }
    .pagination-btns.change-ten-pages {
        display: none !important;
    }
}
@media only screen and (max-width: 600px) {
    .pokemon-holder, .pokemon-moves-holder {
        grid-template-columns: 1fr 1fr;
    }
}
@media only screen and (max-width: 400px) {
    .container.single-pokemon {
        width: 90%;
    }
    .App-header h1 {
        visibility: hidden;
    }
    .pokemon-holder, .pokemon-moves-holder {
        grid-template-columns: 1fr;
    }

}
